export const required = () => (v: any) => !!v

/* String detection */
export const email = () => (v: string) =>
  /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v)

/* Input Length */
export const minlen = (compareTo: number) => (v: string) =>
  v && v.length >= compareTo
export const maxlen = (compareTo: number) => (v: string) =>
  v && v.length <= compareTo

/* Input value comparators */
export const equal = (compareTo: any) => (v: any) => v && v === compareTo
export const gt = (compareTo: any) => (v: any) => v > compareTo
export const lt = (compareTo: any) => (v: any) => v < compareTo
export const gte = (compareTo: any) => (v: any) => v >= compareTo
export const lte = (compareTo: any) => (v: any) => v <= compareTo
export const notEqual = (compareTo: any) => (v: any) =>
  v && v.length !== compareTo
