/* eslint-disable no-unused-vars */
import { sprintf } from 'sprintf-js'
import ValidationError from './validationError'
import { IValidator, ICreateValidator } from './interfaces'

/* Model's field's validation utilities */
const createValidator: ICreateValidator = function ({
  test,
  errorMessage,
}: IValidator) {
  return (value: any): boolean => {
    if (test(value, this._model)) return true

    const errMsg = sprintf(errorMessage, { ...this })
    throw new ValidationError(errMsg, this, test)
  }
}

export function combineValidators(validators: IValidator[], value: any) {
  const mappedValidators = validators.map((validator) =>
    createValidator.call(this, validator)
  )
  try {
    mappedValidators.forEach((v) => v.call(this, value))
    return true
  } catch (e) {
    return e
  }
}
