// eslint-disable-next-line no-unused-vars
import { IModel, IFieldOptions } from './interfaces'
import { mapValues, get } from 'lodash'
import Field from './field'

interface IPrototype {
  prototype: any
}
type IExtendedFieldType = IPrototype & Field

type IExtendedFieldOptions = IFieldOptions & {
  type?: IExtendedFieldType
} & any

function createField(
  model: IModel,
  modelField: Partial<IExtendedFieldOptions>
): IExtendedFieldOptions {
  if (!modelField.name) throw new Error('Models name is required')
  const { type } = modelField
  const XField =
    type && type.prototype && type.prototype instanceof Field ? type : Field
  return new XField(model, modelField)
}

function defineFieldsAsProperties() {
  Object.defineProperties(
    this,
    mapValues(this._fields, (field) => {
      return {
        enumerable: true,
        configurable: true,
        get() {
          return field
        },
      }
    })
  )
}

export function initFields(defaultData: { [name: string]: any }) {
  this._fields = mapValues(this.modelOptions, (modelField, name) => {
    const value = get(defaultData, name, undefined)
    const field = createField(this, { ...modelField, name })

    field.on('change', (f: Field) => {
      this.trigger('change', this, f)
    })

    if (value === undefined) return field

    field.initValue(value)
    return field
  })
  defineFieldsAsProperties.call(this)
}
