/* eslint-disable no-unused-vars */
import { mapValues, pickBy } from 'lodash'
import EventEmitter from '../utils/eventEmitter'
import { initFields } from './initFields'
import {
  IField,
  HTMLElementEvent,
  ModelOptions,
  IModel,
  IModelOptions,
} from './interfaces'

class Model extends EventEmitter implements IModel {
  private _fields: {
    [name: string]: IField
  }

  public modelOptions: IModelOptions

  get data() {
    return mapValues(this._fields, (f) => f.value)
  }

  set data(value) {
    this.setData(value)
  }

  get isValid() {
    return Object.keys(this._fields).every(
      (name) => this._fields[name].validated && this._fields[name].isValid
    )
  }

  get fields() {
    return this._fields
  }

  get errors() {
    if (
      !Object.keys(this._fields).every((name) => this._fields[name].validated)
    ) {
      this.validate()
    }
    const errors = mapValues(
      pickBy(this._fields, (field) => !!field.error),
      (field) => field.error
    )
    return Object.keys(errors).length ? errors : undefined
  }

  constructor(model: ModelOptions, defaultData: { [name: string]: any }) {
    super()
    this.initEvents(['change'])
    this.modelOptions = model(this)
    initFields.call(this, defaultData)
    if (!this._fields) throw new Error('Model is empty')
  }

  extractFromEvent(
    e: HTMLElementEvent<
      | HTMLInputElement
      | HTMLTextAreaElement
      | HTMLSelectElement
      | HTMLButtonElement
    >
  ) {
    const {
      target: { name },
    } = e

    let field
    if (!(field = this._fields[name])) return

    if (field.extractFromEvent) field.extractFromEvent(e)
  }

  setData(data: { [name: string]: any }) {
    if (!data || typeof data !== 'object') return
    let changed = false
    Object.keys(data).forEach((name) => {
      const value = data[name]
      const field = this._fields[name]
      if (value === undefined || !field || field.value === value) return

      this._fields[name].setValue(value)
      changed = true
    })
    if (changed) this.trigger('change', this)
  }

  clearData() {
    let changed = false
    Object.keys(this._fields).forEach((name) => {
      const field = this._fields[name]
      if (field.value !== undefined) {
        this._fields[name].clearValue()
        changed = true
      }
    })
    if (changed) this.trigger('change', this)
  }

  validate() {
    mapValues(this._fields, (f) => f.validate())
    this.trigger('change', this)
    return this.isValid
  }
}

export default Model
