/* eslint-disable no-unused-vars */
import { IModel, ModelOptions } from './interfaces'
import { useState, useEffect } from 'react'
import { values } from 'lodash'
import Model from './model'

export default (model: ModelOptions, defaultData: { [name: string]: any }) => {
  const initModel = new Model(model, defaultData)

  const [formState, setFormState] = useState(initModel)

  const deps = values(formState.data)
  useEffect(() => {
    const change = (newModel: IModel) => {
      const newFormState = Object.assign(
        Object.create(Object.getPrototypeOf(newModel)),
        newModel
      )
      setFormState(newFormState)
    }
    formState.on('change', change)
    return () => {
      formState.off('change', change)
    }
  }, deps)
  return formState
}
