/* eslint-disable no-unused-vars */
import EventEmitter from '../utils/eventEmitter'
import { combineValidators } from './combineValidators'
import {
  IValidator,
  IFieldOptions,
  IField,
  IModel,
  HTMLElementEvent,
  IValidationError,
} from './interfaces'

/**
 * Model's field
 * @property {ValidationError} error Error throw after recent failed validation. Null if no error
 * @property {bool} validated Whether the field is validated or not, result can be error or success
 * @property {string} name Field name
 * @property {mixed} value Field value, depends on implementation of field input component
 */
export default abstract class Field extends EventEmitter implements IField {
  private _value: any

  private _validators: IValidator[]

  private _sideEffects?(
    model: IModel,
    field: IField,
    conflictCheck: Array<string>
  ): void

  private _model: IModel

  get value() {
    return this._value || ''
  }

  set value(value) {
    this.setValue(value)
  }

  public name: string

  public validated?: boolean

  public error?: IValidationError

  get isValid() {
    return !this.error
  }

  constructor(
    model: IModel,
    { validators, sideEffects, ...fieldProps }: IFieldOptions
  ) {
    super()
    this.initEvents(['validated', 'change'])

    this.validated = !validators

    Object.assign(this, fieldProps)
    this._validators = validators
    this._sideEffects = sideEffects
    this._model = model
  }

  /**
   * Set validation status and return validated result
   * @param {*} value
   */
  validate(value?: any) {
    if (!this._validators) {
      this.validated = true
      this.error = undefined
      return true
    }
    const validated = combineValidators.call(
      this,
      this._validators,
      value !== undefined ? value : this.value
    )
    this.validated = true
    if (validated === true) {
      this.error = undefined
    } else {
      this.error = validated
    }
    this.trigger('validated', this.error)
    return !this.error
  }

  /**
   * Set value without validating
   * @param {*} value
   */
  initValue(value: any) {
    this.error = undefined
    this.validated = !this._validators
    this._value = value
  }

  clearValue() {
    this.initValue(undefined)
  }

  extractFromEvent(
    e: HTMLElementEvent<
      | HTMLInputElement
      | HTMLTextAreaElement
      | HTMLSelectElement
      | HTMLButtonElement
    >
  ) {
    const {
      target: { value },
    } = e
    this.setValue(value)
  }

  /**
   * Set value after validate, trigger onChange event after field set
   * @param {*} value
   */
  setValue(value: any, conflictCheck: Array<string> = []) {
    if (conflictCheck.includes(this.name)) return

    if (this.value === value) return
    this.initValue(value)
    this.validate(value)

    conflictCheck.push(this.name)

    if (typeof this._sideEffects === 'function') {
      this._sideEffects(this._model, this, conflictCheck)
    }
    this.trigger('change', this)
  }
}
