/* eslint-disable no-unused-vars */
import { IEvenEmitter } from './../model/interfaces'
import { IEvent } from '../model/interfaces'

const hasOwnProperty = (obj: object, prop: string) =>
  Object.prototype.hasOwnProperty.call(obj, prop)

export default class EventEmitter implements IEvenEmitter {
  private _events: IEvent = {}

  protected initEvents(events: string[]) {
    events.forEach((e) => {
      this._events[e] = []
    })
  }

  public on(event: string, handler: Function) {
    if (!hasOwnProperty(this._events, event))
      throw new Error('Event is not implemented')
    this._events[event].push(handler)
  }

  public off(event: string, handler: Function) {
    if (!hasOwnProperty(this._events, event))
      throw new Error('Event is not implemented')
    this._events[event] = this._events[event].filter((h) => h !== handler)
  }

  public trigger(event: string, ...payload: any[]) {
    if (!hasOwnProperty(this._events, event))
      throw new Error('Event is not implemented')
    this._events[event].forEach((handler) => {
      if (typeof handler === 'function') handler.call(this, ...payload)
    })
  }
}
